import "regenerator-runtime/runtime"
import * as nearAPI from "near-api-js"
import Big from 'big.js'


const contractName = "dev-1636833518084-68644000703536";

const nearConfig = {
    networkId: 'testnet',
    nodeUrl: 'https://rpc.testnet.near.org',
    contractName,
    walletUrl: 'https://wallet.testnet.near.org',
    helperUrl: 'https://helper.testnet.near.org'
}
let near
let walletConnection
let contract

// eslint-disable-next-line
const BOATLOAD_OF_GAS = Big(3).times(10 ** 13).toFixed();

async function connect() {

    near = await nearAPI.connect({
        deps: {
            keyStore: new nearAPI.keyStores.BrowserLocalStorageKeyStore()
        },
        ...nearConfig
    })

    walletConnection = new nearAPI.WalletConnection(near)

    contract = await new nearAPI.Contract(walletConnection.account(), contractName, {
       viewMethods: ["get_player", "get_status"],
        changeMethods: ["create_player"],
        sender: walletConnection.getAccountId()
    })

    console.log(walletConnection.getAccountId())
}


function printWelcome() {
    if (walletConnection.getAccountId()) {
        document.getElementById("account").innerText = walletConnection.getAccountId()
        document.getElementById("login").style.display = "none"
    } else {
        document.getElementById("logout").style.display = "none"
    }
}

async function load() {
    await connect()
    printWelcome()
   
}

export function login() {
    walletConnection.requestSignIn(
      contractName,
      'NEAR Counter / Rust'
    )
}

export function logout() {
    walletConnection.signOut()
    window.location.replace(window.location.origin + window.location.pathname)
}

export async function sendAction() {
    const operation = document.getElementById("operation").value
    const param = parseInt(document.getElementById("param").value)

    // eslint-disable-next-line
    const res = await contract.action({operation, param})
}

export async function createPlayer()
{
    await contract.create_player();
    document.getElementById("operation").innerText = "player created";
}

export async function getPlayer()
{
    let id = document.getElementById("id-input").value
    let player = await contract.get_player({player_id: id});
    document.getElementById("operation").innerText = player;
}

export async function getStatus()
{
    let status = await contract.get_status();
    document.getElementById("operation").innerText = status;

} 

load().catch(err => console.error(err))


